.loader {
  border: 8px solid rgba(0, 0, 0, .1);
  border-top-color: var(--accent-color);
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin: 0 auto;
  animation: spin 1s ease-in-out infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

.header {
  border-bottom: 1px solid #ececec;
}

.header__container {
