  padding-top: 11px;
  padding-bottom: 11px;
  padding-left: 62px;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.18;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
  position: relative;
}

.open-mob-menu {
  background-color: rgba(0, 0, 0, 0);
  border: 0;
  justify-content: center;
  align-items: center;
  margin-left: 16px;
