  margin-left: 14px;
}

.open-login-form:hover, .open-login-form:focus {
  stroke: var(--accent-color);
}

.close-mob-menu {
  background-color: rgba(0, 0, 0, 0);
  border: 0;
  display: block;
  position: absolute;
  top: 22px;
  right: 16px;
}

